import React, { FunctionComponent, MouseEvent } from 'react';
import cx from 'classnames';
import ChevronS1Right from '../Icons/ChevronS1Right';
import ChevronS2Right from '../Icons/ChevronS2Right';
import Touchable, { TouchableProps } from '../Touchable';

import styles from './Button.module.scss';

export interface Props extends TouchableProps {
  animatedCaret?: boolean;
  onClick?: (e: MouseEvent) => void;
  secondary?: boolean;
  tertiary?: boolean;
  submitting?: boolean;
  size?: 32 | 40 | 48 | 56;

  // deprecated props
  large?: boolean;
  small?: boolean;
}

export const Button: FunctionComponent<Props> = ({
  animatedCaret,
  children,
  centered,
  className,
  disabled,
  inline,
  submitting,
  large,
  role,
  secondary,
  small,
  size,
  tertiary,
  // important! props that are destructured here that Touchable
  // still relies on must be passed to Touchable
  ...restProps
}) => (
  <Touchable
    block={!inline}
    className={cx(
      styles.button,
      disabled && styles.disabled,
      inline && styles.inline,
      (size === 56 || large) && styles.size56,
      size === 40 && styles.size40,
      (size === 32 || small) && styles.size32,
      submitting && styles.submitting,
      secondary && styles.secondary,
      tertiary && styles.tertiary,
      (centered || tertiary) && styles.centered,
      className,
    )}
    disabled={disabled || submitting}
    role={role ? role : 'button'}
    inline={inline}
    centered={centered || tertiary}
    {...restProps}
  >
    <span className={cx(styles.flex)}>
      <span className={styles.text}>{children}</span>
      {!centered && !tertiary && (
        <span
          className={cx(styles.caret, animatedCaret && styles.animatedCaret)}
        >
          {size === 32 || small ? (
            <ChevronS2Right
              className={cx(styles.icon, animatedCaret && styles.secondaryIcon)}
            />
          ) : (
            <ChevronS1Right
              className={cx(styles.icon, animatedCaret && styles.secondaryIcon)}
            />
          )}
          {animatedCaret &&
            (size === 32 || small ? (
              <ChevronS2Right className={styles.icon} />
            ) : (
              <ChevronS1Right className={styles.icon} />
            ))}
        </span>
      )}
    </span>
  </Touchable>
);

// Button.defaultProps = { appearance: Appearance.PRIMARY };
Button.displayName = 'Button';
export default Button;
export { styles };
